import {
  ADD_PRODUCT,
  REMOVE_PRODUCT,
  CHANGE_PRODUCT_QUANTITY,
} from './actionTypes';

const addProduct = product => ({
  type: ADD_PRODUCT,
  payload: product,
});

const removeProduct = product => ({
  type: REMOVE_PRODUCT,
  payload: product,
});

const changeProductQuantity = product => ({
  type: CHANGE_PRODUCT_QUANTITY,
  payload: product,
});

export {addProduct, removeProduct, changeProductQuantity};
